<template>
  <section>
    <img
      src="https://s11.ax1x.com/2024/02/17/pFJkI2V.jpg"
      @load="goToBranch()"
      @click="goToBranch(0)"
    />
  </section>
</template>

<script lang="ts" setup>
import { useRouter } from "vue-router";

const router = useRouter();
let timer: number = 0;
function goToBranch(delay = 1000) {
  timer && clearTimeout(timer);
  timer = setTimeout(() => {
    router.push("/branch");
  }, delay);
}
</script>

<style lang="scss" scoped>
section {
  height: calc((100vh - var(--safe-top) - var(--margin)));
  display: flex;
  align-items: center;
  justify-content: center;

  img {
    max-height: 50%;
    max-width: 50%;
    user-select: none;
    -webkit-user-drag: none;

    @media (prefers-color-scheme: dark) {
      filter: invert(1);
    }
  }
}
</style>
